.properties-panel {
  position: fixed;
  top: 130px; /* 탭바(38px) + 메뉴바(48px) + 헤더(44px) = 130px */
  right: 50px; /* 우측 메뉴(40px) + 여백(10px) */
  bottom: 0;
  width: 380px; /* 300px에서 380px로 확대 */
  background: #fff;
  border-left: 1px solid #e0e0e0;
  box-shadow: -2px 0 8px rgba(0, 0, 0, 0.1);
  padding: 16px;
  overflow-y: auto;
  z-index: 1000;
  transition: transform 0.25s cubic-bezier(.4, 1.4, .6, 1), opacity 0.25s cubic-bezier(.4, 1.4, .6, 1);
  transform: translateX(0);
  opacity: 1;
}l {
  position: fixed;
  top: 130px; /* 탭바(38px) + 메뉴바(48px) + 헤더(44px) = 130px */
  right: 10px; /* 우측 여백 추가 */
  bottom: 0;
  width: 380px; /* 300px에서 380px로 확대 */
  background: #fff;
  border-left: 1px solid #e0e0e0;
  box-shadow: -2px 0 8px rgba(0, 0, 0, 0.1);
  padding: 16px;
  overflow-y: auto;
  z-index: 1000;
  transition: transform 0.25s cubic-bezier(.4, 1.4, .6, 1), opacity 0.25s cubic-bezier(.4, 1.4, .6, 1);
  transform: translateX(0);
  opacity: 1;
}

.properties-panel.closed {
  transform: translateX(100%);
  opacity: 0;
  pointer-events: none;
}

.sidebar-header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 8px 8px 16px 0; /* 하단 패딩을 늘려서 공간 확보 */
  margin-bottom: 8px; /* 마진 추가 */
  border-bottom: 1px solid #f0f0f0; /* 구분선 추가 */
}

.close-panel-btn {
  background: none;
  border: none;
  font-size: 16px; /* 14px에서 16px로 크기 증가 */
  cursor: pointer;
  width: 32px; /* 24px에서 32px로 크기 증가 */
  height: 32px; /* 24px에서 32px로 크기 증가 */
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #666; /* #888에서 #666으로 더 진하게 */
  border-radius: 6px; /* 4px에서 6px로 증가 */
  transition: background 0.15s, color 0.15s;
  margin-left: auto; /* 오른쪽 정렬 강화 */
}

.close-panel-btn:hover {
  background: #f0f0f0;
  color: #333; /* 호버 시 더 진한 색상 */
}

.panel-section {
  margin-bottom: 16px;
}

.panel-title {
  font-size: 15px;
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
}

.panel-title.collapsible {
  cursor: pointer;
  font-size: 13px;
  color: #444;
  font-weight: 600;
  margin-bottom: 8px;
}

.property-type {
  font-size: 13px;
  color: #444;
  margin-bottom: 8px;
}

.property-value {
  font-size: 13px;
  color: #888;
  margin-bottom: 12px;
}

.divider {
  border: none;
  border-top: 1px solid #eee;
  margin: 8px 0;
}

.font-controls {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}

.property-input {
  width: 100%;
  padding: 6px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 12px;
}

.font-type {
  width: 100px;
}

.font-size {
  width: 50px;
}

.color-input {
  width: 28px;
  height: 28px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.style-buttons, .alignment-buttons, .action-buttons {
  display: flex;
  gap: 8px;
  margin-bottom: 8px;
  flex-wrap: wrap;
}

.property-btn {
  flex: 1;
  padding: 6px;
  border: 1px solid #ccc;
  background: #f8f8f8;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 30px;
  height: 30px;
}

.property-btn.active {
  background: #007acc;
  color: white;
  border-color: #007acc;
}

.property-btn:hover {
  background: #e0e0e0;
}

.property-btn.active:hover {
  background: #0056b3;
}

.spacing-controls {
  display: flex;
  gap: 8px;
  margin-bottom: 8px;
}

.spacing-controls .property-input {
  width: 50px;
}

.action-buttons .property-btn {
  flex: 0 0 auto;
  width: 40px;
}
